import axios from 'axios'
import { url_base } from './../../url_base'
export const Global = {

	namespaced: true,

	state: {
		cargando: true,
		noticias: {
			ultimas: [],
			all: [],
			get: null,
			relacionadas: []
		},
		episodios: {
			ultimos1: [],
			ultimos2: [],
			get: null,
			mas: []
		},
		programas: {
			all: []
		},
		programacion: {
			lunes: 		[],
			martes: 		[],
			miercoles: 	[],
			jueves: 		[],
			viernes: 	[],
			sabado: 		[],
			domingo: 	[],
		},
		slider: [],
		url: url_base	
	},

	actions: {

		NOTICIAS_ULTIMAS (context) {
			axios.get(url_base + 'api/noticias/ultimas')
			.then(res => {
				//console.log(res)
				context.commit('SET_ULTIMAS_NOTICIAS', res.data )
			});	
			
			axios.get(url_base + 'api/episodios/ultimos?limit=4')
			.then(res => {
				console.log(res)
				context.commit('SET_ULTIMOS_EPISODIOS1', res.data )
			});

			axios.get(url_base + 'api/episodios/ultimos?limit=4&offset=4')
			.then(res => {
				console.log(res)
				context.commit('SET_ULTIMOS_EPISODIOS2', res.data )
			});
		},

		NOTICIAS (context) {
			axios.get(url_base + 'api/noticias?chunk=4')
			.then(res => {
				//console.log(res)
				context.commit('SET_NOTICIAS', res.data )
			});			
		},

		VER_NOTICIA (context, id) {
			axios.get(url_base + 'api/noticias/' + id)
			.then(res => {
				console.log(res)
				context.commit('SET_NOTICIA', res.data )
			});			
		},

		VER_EPISODIO (context, id) {
			axios.get(url_base + 'api/episodios/' + id)
			.then(res => {
				console.log(res.data.programa.id)
				context.commit('SET_EPISODIO', res.data )
				context.dispatch('VER_MAS_EPISODIOS', {
					programa: res.data.programa.id, 
					id: res.data.id
				})
			});			
		},

		VER_MAS_EPISODIOS (context, data) {
			axios.get(url_base + 'api/episodios/de/'+ data.programa + '/' + data.id)
			.then(res => {
				
				context.commit('SET_MAS_EPISODIOS', res.data )
			});			
		},

		GET_PROGRAMAS (context) {
			axios.get(url_base + 'api/programas')
			.then(res => {
				
				context.commit('SET_PROGRAMAS', res.data )
			});			
		},

		GET_PROGRAMACION (context,id) {
			axios.get(url_base + 'api/programacion/' + id)
			.then(res => {
				context.commit('SET_PROGRAMACION', {
					id: id,
					dia: res.data 
				})
			});			
		},


		VER_RELACIONADAS (context, id) {
			axios.get(url_base + 'api/noticias/?limit=4&except=' + id)
			.then(res => {
				context.commit('SET_RELACIONADAS', res.data )
			});			
		},

		SLIDER_PUBLI (context) {
			axios.get(url_base + 'sliders?activo=true')
			.then(res => {
				console.log(res)
				//context.commit('SET_ULTIMAS_NOTICIAS', res.data )
			});	
		}

		

	},

	mutations: {
		SET_ULTIMAS_NOTICIAS: (state, data) => {
			state.noticias.ultimas = data
		},

		SET_ULTIMOS_EPISODIOS1: (state, data) => {
			state.episodios.ultimos1 = data
		},

		SET_ULTIMOS_EPISODIOS2: (state, data) => {
			state.episodios.ultimos2 = data
		},
		
		SET_NOTICIAS: (state, data) => {
			state.noticias.all = data
		},

		SET_PROGRAMAS: (state, data) => {
			state.programas.all = data
		},
		
		SET_NOTICIA: (state, data) => {
			state.noticias.get = data
		},
		
		SET_EPISODIO: (state, data) => {
			state.episodios.get = data
		},
		
		SET_MAS_EPISODIOS: (state, data) => {
			state.episodios.mas = data
		},
		
		SET_RELACIONADAS: (state, data) => {
			state.noticias.relacionadas = data
		},

		SET_PROGRAMACION: (state, data) => {

			if(data.id == 1){
				state.programacion.lunes = data.dia
			}
			if(data.id == 2){
				state.programacion.martes = data.dia
			}
			if(data.id == 3){
				state.programacion.miercoles = data.dia
			}
			if(data.id == 4){
				state.programacion.jueves = data.dia
			}
			if(data.id == 5){
				state.programacion.viernes = data.dia
			}
			if(data.id == 6){
				state.programacion.sabado = data.dia
			}
			if(data.id == 7){
				state.programacion.domingo = data.dia
			}
			
		},
	}
 
}


