import Vue from 'vue'
import Router from 'vue-router'

const Home = resolve => require(['./../pages/homePage.vue'], resolve)
const Programas = resolve => require(['./../pages/programasPage.vue'], resolve)
const Programacion = resolve => require(['./../pages/programacionPage.vue'], resolve)
const Noticias = resolve => require(['./../pages/noticiasPage.vue'], resolve)
const verNoticia = resolve => require(['./../pages/verNoticiasPage.vue'], resolve)
const verEpisodio = resolve => require(['./../pages/verVideo.vue'], resolve)
const Contacto = resolve => require(['./../pages/contactoPage.vue'], resolve)
const EnVivo = resolve => require(['./../pages/enVivo.vue'], resolve)
const RadioVivo = resolve => require(['./../pages/radioVivo.vue'], resolve)

Vue.use(Router)

export default new Router({
  linkActiveClass: 'current_page_item',
  linkExactActiveClass: 'current_page_item',
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  },
  routes: [
    {path: '/',             redirect: '/inicio'},
    {path: '/inicio',       component: Home,          name: 'inicio'      },
    {path: '/envivo',       component: EnVivo,        name: 'envivo'      },
    {path: '/radiochaco',   component: RadioVivo,     name: 'radiovivo'   },
    {path: '/programas',    component: Programas,     name: 'programas'   },
    {path: '/programacion', component: Programacion,  name: 'programacion'},
    {path: '/noticias',     component: Noticias,      name: 'noticias'    },
    {path: '/noticias/:id', component: verNoticia,    name: 'verNoticia'  },
    {path: '/episodios/:id', component: verEpisodio,  name: 'verEpisodio' },
    {path: '/contacto',     component: Contacto,      name: 'contacto'    }
  ]
})



